.itemContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    border-radius: 5px;
    padding: 5px 15px;
    width: 30%;
}

.itemHeader{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 10px;
}

p{
    font-size: 16px;
}

span{
    font-size: 30px;
    font-weight: bold;
}




@media (max-width: 750px) {
    .itemContainer{
        width: 20%;
    }

    p{
        font-size: 12px;
    }

    span{
        font-size: 20px;
    }

    i{
        display: none;
    }
}