.formContainer{
    max-width: 1120px;
    margin: 20px auto;
    background-color: #fff;
    box-shadow: 0px 0px 15px #ccc;
    border-radius: 5px;
    display: flex;
    justify-content: space-around;
    padding: 15px 5px;
}

.inputContainer{
    display: flex;
    flex-direction: column;
}

input{
    outline: none;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 15px;
    border: 1px solid #ccc;
}

.radioGroup{
    display: flex;
    align-items: center;
}

.radioGroup input{
    margin-left: 5px;
}

.button{
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: teal;
    color: white;
    padding: 5px;
}